$color: #0cf;
.about{
background-color: #0066ff;
 height:100vh;
  background-image: linear-gradient(
    135deg,
    #0066ff 0%,
    #e580ff 100%
  );
  color: white;

  width: 95%;
  margin: 0 auto;

  clip-path: polygon(0% 0%, 100% 0%, 100% 75%, 75% 75%, 75% 100%, 50% 75%, 0% 75%);
  padding-bottom: 10%;


  .section-title{
  	text-align: center;
  	text-transform: capitalize;
    font-size: 3.5rem;
    font-weight: 700;

  }
  .image{
  	display: flex;
    justify-content: center;
  	border-radius: 10px;
    height: 350px;
  	
  }

  .info{
  	margin-top: 15px;
  }

  .button {
    display: inline-block;
    padding: .75rem 1.25rem;
    border-radius: 10rem;
    color: #fff;
    text-transform: uppercase;
    font-size: 1rem;
    letter-spacing: .15rem;
    transition: all .3s;
    position: relative;
    overflow: hidden;
    z-index: 1;
    text-decoration: none;
    &:after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: $color;
        border-radius: 10rem;
        z-index: -2;
    }
    &:before {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0%;
        height: 100%;
        background-color: darken($color, 15%);
        transition: all .3s;
        border-radius: 10rem;
        z-index: -1;
    }
    &:hover {
        color: #fff;
        &:before {
            width: 100%;
        }
    }
}


}

@media (max-width: 868px){

.about{
  height: 100%;
   width: 100vw;
   margin: 0 auto;
  clip-path: polygon(0% 0%, 100% 0%, 100% 86%, 75% 86%, 75% 100%, 51% 86%, 0 86%);
    border-radius: 10px;
  .image{
    display: none;
  }

  .info{
    font-size: 1.3rem;
    padding-bottom: 1.5rem;
  }
  .button {
  display: none;
  }
}

  }